/**
 * camelCase
 */
export interface BaseEntity {
  id: string;
  createdAt: string; // ISO 8601  - camelCase
  updatedAt: string; // ISO 8601  - camelCase
  createdBy?: string; // ID - camelCase
  updatedBy?: string; // ID - camelCase
  deletedAt?: string | null; //  - camelCase
}

export interface PaginationParams {
  limit?: number;
  offset?: number;
  sort?: string;
  order?: 'asc' | 'desc';
}

export interface PaginatedResponse<T> {
  data: T[];
  total: number;
  limit: number;
  offset: number;
}

/**
 * LuckDB APIcamelCase
 *  server/pkg/response/response.go 
 */
export interface APIResponse<T = any> {
  code: number; // 
  message: string; // 
  data: T; // 
  error?: ErrorPayload; // 
  requestId?: string; // ID- camelCase
  timestamp?: string; // 
  durationMs?: number; // - camelCase
  version?: string; // API
}

export interface ErrorPayload {
  details?: any; // 
}

/**
 * camelCase
 */
export interface PaginationMeta {
  total: number;
  page: number;
  limit: number;
  totalPages: number; // camelCase
}

/**
 * 
 */
export interface PaginatedAPIResponse<T = any> extends APIResponse<T[]> {
  pagination?: PaginationMeta;
}

/**
 * @deprecated  APIResponse 
 */
export interface ApiResponse<T = any> {
  success: boolean;
  data: T;
  message?: string;
}

/**
 * @deprecated  ErrorPayload 
 */
export interface ApiError {
  error: string;
  code: string;
  details?: string;
  trace_id?: string;
}

