/**
 *  server/internal/domain/fields/valueobject/field_type.go
 */
export type FieldType =
  // 
  | 'singleLineText' // 
  | 'longText' // 
  | 'text' // 
  | 'number' // 
  | 'singleSelect' // 
  | 'multipleSelects' // 
  | 'select' // 
  | 'multipleSelect' // 
  | 'date' // 
  | 'datetime' // 
  | 'checkbox' // 
  | 'boolean' // 
  | 'url' // 
  | 'email' // 
  | 'phone' // 
  | 'attachment' // 
  | 'rating' // 
  | 'user' // 
  | 'button' // 
  // 
  | 'link' // 
  | 'formula' // 
  | 'rollup' // 
  | 'count' // 
  | 'lookup' // 
  // 
  | 'createdTime' // 
  | 'lastModifiedTime' // 
  | 'createdBy' // 
  | 'lastModifiedBy' // 
  | 'autoNumber'; // 

export interface FieldOptions {
  placeholder?: string;
  helpText?: string;
  choices?: SelectOption[];
  minValue?: number;
  maxValue?: number;
  decimal?: number;
  minLength?: number;
  maxLength?: number;
  pattern?: string;
  dateFormat?: string; //   camelCase
  timeFormat?: string; //   camelCase
  maxFileSize?: number; //   camelCase
  allowedTypes?: string[]; //   camelCase
  linkTableId?: string; //   camelCase
  linkFieldId?: string;
  formula?: string;
  validationRules?: ValidationRule[]; //   camelCase
}

export interface SelectOption {
  id: string;
  name: string;
  color?: string;
}

export interface ValidationRule {
  type: string;
  value: any;
  message?: string;
}

